import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import styles from './_now-iframe.scss';
import {actionTypes} from '@servicenow/ui-core';
import {normalizeUnit} from './normalize-unit';

const {COMPONENT_RENDERED} = actionTypes;

const view = (state) => {
	let {title = '', src, disableSandbox = false} = state.properties;
	return (
		<iframe
			className="now-iframe"
			title={title}
			src={src}
			frameborder="0"
			allow="accelerometer autoplay clipboard-write encrypted-media gyroscope picture-in-picture"
			allowfullscreen
			sandbox={
				disableSandbox
					? 'allow-forms allow-modals allow-popups allow-presentation allow-same-origin allow-scripts allow-downloads'
					: 'allow-scripts'
			}
		/>
	);
};

/**
 * Wraps a native HTML iFrame with additional support for all CSS units for width and height.
 *
 * ```jsx
 * <now-iframe title="My iframe title" width="400" height="150" src="https://www.wikipedia.com"/>
 * ```
 *
 * @seismicElement now-iframe
 * @summary IFrame component with relative width and height support. The width and height properties both support all CSS units. Unitless numeric values default to pixel units.
 * @uib.label iFrame
 * @uib.icon iframe
 * @uib.description IFrame component with relative width and height support.
 * @uib.category primitives
 * @uib.associatedTypes global.core
 * @uib.properties title, src, width, height, disableSandbox
 */
createEnhancedElement('now-iframe', {
	actionHandlers: {
		[COMPONENT_RENDERED]({state, host}) {
			host.style.width = normalizeUnit(state.properties.width);
			host.style.height = normalizeUnit(state.properties.height);
		}
	},
	properties: {
		/**
		 * IFrame title. Required. 
		 * @type {string}
		 * @uib.fieldType string
		 * @uib.required true
		 * @uib.label title
		 * @uib.description A meaningful title that best describes the contents of the embedded page.
		 * @uib.defaultValue Wikipedia home page
		 **/
		title: {required: true, schema: {type: 'string'}},

		/**
		 * Required. The URL of the page to embed.
		 * @type {string}
		 * @uib.fieldType string
		 * @uib.required true
		 * @uib.label source
		 * @uib.description The URL of the page to embed.
		 * @uib.defaultValue https://www.wikipedia.com
		 */
		src: {required: true, schema: {type: 'string'}},

		/**
		 * Width of iFrame, supports all CSS units. Required. 
		 * @type {(string|number)}
		 * @uib.fieldType string
		 * @uib.label width
		 * @uib.description IFrame width in CSS units or numeric value representing pixels.
		 * @uib.defaultValue 400
		 */
		width: {default: 400, schema: {type: ['string', 'number']}},

		/**
		 * Height of the iFrame. Supports all CSS units.
		 * @type {(string|number)}
		 * @uib.fieldType string
		 * @uib.label height
		 * @uib.description IFrame height in CSS units or numeric value representing pixels.
		 * @uib.defaultValue 300
		 */
		height: {default: 300, schema: {type: ['string', 'number']}},

		/**
		 * When true, removes these sandbox restrictions:
		 * allow-forms, allow-modals, allow-popups, allow-presentation, allow-same-origin, 
		 * allow-scripts, allow-downloads. By default, only the allow-scripts restriction is removed.
		 * @type {boolean}
		 * @uib.label Disable Sandbox
		 * @uib.description Set to disable the following restrictions: allow-forms, allow-modals, allow-popups, allow-presentation, allow-same-origin, allow-scripts, allow-downloads. By default, only the allow-scripts restriction is removed.
		 */
		disableSandbox: {default: false, schema: {type: 'boolean'}}
	},
	view,
	styles
});
