const UNIT_REGEX = /^(\d+\.?\d*)(cm|mm|in|px|pt|pc|%|em|ex|ch|rem|vw|vh|vmin|vmax)?$/;

export function usingCSSUnits(value) {
	return UNIT_REGEX.test(value);
}

export function normalizeUnit(value, defaultValue = 400) {
	if (!value || !usingCSSUnits(value)) return `${defaultValue}px`;

	// eslint-disable-next-line no-unused-vars
	let [original, numericValue, unit] = UNIT_REGEX.exec(value);

	numericValue = parseInt(numericValue);

	if (!unit) {
		return `${numericValue}px`;
	}

	return `${numericValue}${unit}`;
}
